import {BrowserRouter, Routes, Route, Navigate} from "react-router-dom";
import {useEffect, useState} from 'react';
import {ethers} from 'ethers';
// import Web3 from 'web3';

import './App.css';
import Login from "./components/login/login";
import Profile from "./components/profile/profile";

export default function App() {
    const [haveMetamask, setHaveMetamask] = useState(true);
    const [address, setAddress] = useState(null);
    const [network, setNetwork] = useState(null);
    const [balance, setBalance] = useState(1);
    const [isConnected, setIsConnected] = useState(false);
    const [isError, setIsError] = useState(0);
    
    const {ethereum} = window;
    const provider = new ethers.providers.Web3Provider(window.ethereum);

    useEffect(() => {
        const { ethereum } = window;
        const checkMetamaskAvailability = async () => {
            if (!ethereum) {
                setHaveMetamask(false);
            }
            setHaveMetamask(true);
        };
        checkMetamaskAvailability();
    }, []);

    const connectWallet = async () => {
        try {
            if (!ethereum){
                setHaveMetamask(false);
            }
            const accounts = await ethereum.request({
                method: 'eth_requestAccounts',
            });
            const chainId = await ethereum.request({
                method: 'eth_chainId',
            });

            let balanceVal = await provider.getBalance(accounts[0]);
            let bal = ethers.utils.formatEther(balanceVal);

            if (chainId == 0x3){
                setNetwork('Ropsten Test Network');
            }
            else {
                setNetwork('Other Test Network');
            }
            setAddress(accounts[0]);
            setBalance(bal);
            setIsConnected(true);
        }
        catch (error){
            setIsConnected(false);
            setIsError(true);
        }
    }

    return (
        <BrowserRouter>
            <div className="App">
            {
                isConnected == false ?
                <Login isHaveMetamask = {haveMetamask} connectTo = {connectWallet}/> :
                <Navigate to = "/profile" />
                //<Profile />
            }
            <Routes>
                <Route 
                    path = "/profile" 
                    element = {<Profile address = {address} networkType = {network} balance = {balance} />}
                ></Route>
            </Routes>
            </div>
        </BrowserRouter>
    )
}


const LoggedInPage = () => {
    return (
        <BrowserRouter>
            <Routes>
                <Route path = "/profile" element = {<Profile />}></Route>
            </Routes>
        </BrowserRouter>
    )
    
}