import {Link} from "react-router-dom";

import "./profile.css";
import "../../global.css";
import METAMASK from '../../images/METAMASK.png';

export default function Profile(props){

    return (
        <div className = "profile-background">
            <div className = "profile">
                <img src = {METAMASK} alt = "logo" height = "100%"/>
                <div className = "profile-account">
                    <p>
                        <b>Profile details</b>
                    </p>
                    <hr></hr>
                    <p>
                        Address:&nbsp;
                        <span className = "profile-message">{props.address}</span>
                        <br></br>
                        Network:&nbsp;
                        <span className = "profile-message">{props.networkType}</span>
                        <br></br>
                        Balance:&nbsp;
                        <span className = "profile-message">{props.balance}</span>
                        &nbsp;ETH
                    </p>
                </div>
            </div>
            <div className = "profile-toolbar">
                <Link to = "/">Back</Link> | 
                <Link to = "/">profile</Link>
            </div>
        </div>
    )
}